import React from "react";
import "./HeroInfo.css";
import CardHero from "./CardHero";
import { Grid } from "@material-ui/core";


const HereoInfo = ({ datos = [] }) => {

    
    return (
        <div className="o-info">
            <h1 className="o-title">PERSONAJES</h1>
            <Grid container spacing={10} justify="center">
                {datos.map((heros) => {
                    return <CardHero name={heros.name} image={heros.thumbnail.path + ".jpg"} comics={heros.urls} des={heros.description}/>
                })}
                </Grid>
        </div>
    );
};
export default HereoInfo;
