import React, { useState } from 'react';
import { Card, Typography, CardMedia, CardContent, CardActions, IconButton, Collapse } from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'
import clsx from 'clsx';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Links from './Links';
import "./HeroInfo.css";
// import CardActionArea from '@material-ui/core/CardActionArea';


const CardHero = ({ name, image, comics, des, classes}) => {

  const [expanded, setExpanded] = React.useState(false);

    const handleExpandClick = () => {
        setExpanded(!expanded);

      };

    return (
        <Card className={classes.item}>
            {/* <CardActionArea> */}
                <CardMedia className={classes.media} image={image} />
                <CardContent>
                    <Typography className={classes.typography}>{name}</Typography>
                </CardContent>
            {/* </CardActionArea> */}
            <CardActions disableSpacing>
            <h1 className={classes.info}>Más información</h1>
            <IconButton
          className={clsx(classes.expand, {
            [classes.expandOpen]: expanded,
          })}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="show more"
        >
            
          <ExpandMoreIcon className="o-icon"/>
        </IconButton>
      </CardActions>
      <Collapse in={expanded} timeout="auto" unmountOnExit>
        <CardContent className={classes.paragraph}>
        <Typography className={classes.des}>{des}</Typography>
            {comics.map((info)=>{return <Links comic={info.type} link={info.url}/>})}
        </CardContent>
      </Collapse>
        </Card>
    );
}

export default withStyles({
    item: {
        minWidth: "350px",
        margin: "1em",
        boxSizing: "border-box",
        textAlign: "center",
        backgroundColor: "rgb(0, 0, 0, 0.5)"
    },
    media: {
        height: "350px"
    },
    typography: {
        fontFamily: "'Oswald', sans-serif",
        fontSize: "18px",
        color: "khaki",

    },
    title: {
        color: "#fff",
    },
    
    paragraph: {
        color: "#fff",
        paddingLeft: "25px",
    },
    
    des:{
        width: "300px",
        textAlign: "start",
        fontSize: "14px",
        paddingBottom: "1em"
    }
    ,
      expand: {
          transform: 'rotate(0deg)',
          marginLeft: 'auto',
          color: "#fff",
      },
      expandOpen: {
        transform: 'rotate(180deg)',
        color: "#fff",
      },
      info: {
          color: "#ccc",
          fontSize: "14px",
          paddingLeft: '15px',

      }
})(CardHero);